.Resizable {
    border: 2px lightgray;
    border-style: none solid;
    resize: horizontal;
    overflow: auto;
    min-width: 80px;
    width: 350px;
    -webkit-transition: width 200ms;
    -o-transition: width 200ms;
    transition: width 200ms;
}



